@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    font-family: Roboto, Arial, sans-serif;
}



.App input.ssearch[type="search"] {
    margin: 0 auto;
    width: 50%;
    display: block;
    padding: 15px 20px;
    border: 0;
    box-shadow: 0 0 3px #ddd;
    font-size: 20px;
    font-weight: 700;
    margin-top: 15px;
}

.App {
    margin:  0 auto;
    width: auto;
}

/* Medium Layout: 1280px. */
@media only screen and (min-width: 1170px) and (max-width: 1920px) { 
  
    .App {
        width: 1170px;
    }
}
/* Tablet & mobile Layout: 768px. */
@media only screen and (max-width: 1184px) { 
    .App{
        width: 970px;
    }
    
    .video-container > .single-video-item {
        width: 30.24%;
    }
}

/* Tablet Layout: 768px. */
@media only screen and (min-width: 768px) and (max-width: 991px) { 
    .App{
        width: 750px;
    }
   
    .video-container > .single-video-item {
        width: 46%;
    }
}
/* Mobile Layout: 320px. */
@media only screen and (max-width: 767px) { 
    
    .App {
        padding-right: 15px;
        padding-left: 15px;
        margin-right: auto;
        margin-left: auto;
        width: auto;
    }
    .video-container > .single-video-item {
        width: 45.8%;
    }
    
}
/* Wide Mobile Layout: 480px. */
@media only screen and (min-width: 480px) and (max-width: 767px) { 
    .App {
        padding-right: 15px;
        padding-left: 15px;
        margin-right: auto;
        margin-left: auto;
        width: auto;
    }

    .video-container > .single-video-item {
        max-width: auto !important;
        width: auto !important;
        display: block !important;
        margin: auto 20%;
    }
   
}


.video-container {
    display: block;
    margin:0 auto;
}

.video-container  .single-video-item:hover {
    opacity: 0.8;
}


.video-container > .single-video-item:hover .video-title > h3:after {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    color: #909090;
    font-family: fontAwesome;
    content: "\f142";
    font-size: 16px;
}


/* Wide Mobile Layout: 480px. */
@media only screen and (min-width: 480px) and (max-width: 767px) { 
    .App {
        padding-right: 15px;
        padding-left: 15px;
        margin-right: auto;
        margin-left: auto;
        width: auto;
    }

    .video-container > .single-video-item {
        max-width: 100% !important;
        width: auto !important;
        display: block !important;
        margin: auto 20%;
    }

    .App input.ssearch[type="search"] {
        
        padding: 10px 20px;
        
        font-size: 15px;
    }
    .video-title h3 {
        font-size: 15px;
    }
}



.video-container > .single-video-item {
    max-width: 360px;
    display: inline-table;
    padding: 15px;
    transition: .3s ease-in-out;
    cursor: pointer;
}


.single-video-thumbnail img {
    max-height: 202px;
    width: 100%;
}


.video-title h3 {
    color: #030303;
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    white-space: normal;
    -webkit-line-clamp: 2;
    font-weight: 500;
    line-height: 1.6rem;
    letter-spacing: normal;
    font-size: 20px;
    font-weight: 500;
    text-align: left;
    padding-left: 15px;
    margin-bottom: 0;
    position: relative;
    padding-right: 25px;
}
.video-title img {
    float: left;
    width: 48px;
    height: 48px;
    border-radius: 100%;
}
p{
    color: #606060;
    font-size: 8px;
}
.video-container > div > p {
    text-align: left;
    padding-left: 63px;
    font-size: 12px;
    margin-bottom: 0;
    padding-bottom: 0;
}

p.chdesc {
    margin-top: 5px;
}
p.channel-name > i.fa {
    background: #606060;
    color: #fff;
    border-radius: 100%;
    margin-left: 5px;
    font-size: 8px;
    line-height: 8px;
    padding: 3px;
}

p.channel-name {
    font-size: 14px !important;
    
}



